import React, { Component, Fragment } from "react";
import { MdAdd, MdArrowBack, MdClose, MdInfoOutline } from "react-icons/md";
import { Auth, SchoolData } from "../../actions";
import { SchoolPositionApiResponse } from "../../types/HeadTeacherRequesTypes";
import TextInput from "../Inputs/TextInput";
// import { BsFillCheckSquareFill } from "react-icons/bs";
import { FiSearch } from "react-icons/fi";
// import { ImCheckboxUnchecked } from "react-icons/im";
import { APP_URL } from "../../config/app.config";
import axios from "axios";
import { setAxiosToken } from "../../utils/AxiosToken";
import { PositionsOnSchool } from "../../containers/Head-Teacher/Request/HeadTeacherRequestToDee";
import { search } from "../../utils/functions";
import Button from "../Buttons/Button";
import { errorToText } from "../../utils/errors";
import Modal, { ModalSize, Themes } from "../Modal/Modal";
import Loading from "../Loading/Loading";

interface PositionStaffOnDelete {
  first_name: string;
  full_name: string;
  last_name: string;
  middle_name: string;
  nid: string;
  phone_numbers: string;
  staff_code: string;
  user_id: string;
}

interface AddSchoolPositionsProps {
  auth: Auth;
  positions: SchoolPositionApiResponse[];
  schoolPositions: PositionsOnSchool[];
  appendPositionsToSchool: (
    schoolPositions: SchoolPositionApiResponse[]
  ) => void;
  removePositionsToSchool: (school_has_position_id: string) => void;
  close?: () => void;
  school?: SchoolData;
}

interface AddSchoolPositionsState {
  searchPositionValue: string;
  schoolPositionsTemp: SchoolPositionApiResponse[];
  edit: boolean;
  loading: boolean;
  success: string;
  error: string;
  selected_position_users_on_delete: {
    position: SchoolPositionApiResponse;
    data: PositionStaffOnDelete[];
  } | null;
}

export class AddSchoolPositions extends Component<
  AddSchoolPositionsProps,
  AddSchoolPositionsState
> {
  constructor(props: AddSchoolPositionsProps) {
    super(props);

    this.state = {
      searchPositionValue: "",
      schoolPositionsTemp: [],
      edit: false,
      loading: false,
      success: "",
      error: "",
      selected_position_users_on_delete: null,
    };
  }
  addPositionToSchool = (position: SchoolPositionApiResponse) => {
    this.setState({
      schoolPositionsTemp: [
        position,
        ...this.state.schoolPositionsTemp.filter(
          (itm) => itm.position_id !== position.position_id
        ),
      ],
    });
  };
  // Filter by positions having teachers
  FilterByPositionsHavingTeacher = (): SchoolPositionApiResponse[] => {
    const Positions = this.state.schoolPositionsTemp;
    let temp = [];
    temp = Positions.sort((a, b) => {
      let count: number = 0;
      const testPosition = this.props.schoolPositions.find(
        (itm) => itm.position_code === a.position_code
      );
      if (testPosition !== undefined) {
        count = parseInt(testPosition.total_on_position);
      }
      if (count > 0) {
        return -1;
      } else {
        return 1;
      }
    });
    return temp;
  };
  addSchoolPositions = async () => {
    if (
      window.confirm("Are you sure do you want to add these positions?") ===
      true
    ) {
      if (
        this.state.schoolPositionsTemp.length > 0 &&
        this.props.school === undefined
          ? this.props.auth.school
          : this.props.school
      ) {
        setAxiosToken();
        let newPositions = [];
        this.setState({ loading: true });
        for (let newP of this.state.schoolPositionsTemp) {
          newPositions.push({
            school_code:
              this.props.school === undefined
                ? this.props.auth.school!.school_code
                : this.props.school.school_code,
            position_code: newP.position_code,
          });
        }
        console.log("Positions codes: ", newPositions);
        try {
          await axios.post(`${APP_URL}/schoolposition/assign`, newPositions);
          this.props.appendPositionsToSchool(this.state.schoolPositionsTemp);
          this.setState({ schoolPositionsTemp: [] });
          this.setState({ loading: false });
          this.setState({
            success: "Positions added successfully!",
            error: "",
          });
          alert("Positions added successfully!");
        } catch (error: any) {
          this.setState({ loading: false });
          this.setState({ error: errorToText(error) });
          console.log("Err: ", { ...error });
        }
      }
    }
  };
  RemoveSchoolPosition = async (
    school_has_position_id: string,
    school_code: string
  ) => {
    this.setState({ loading: true });
    try {
      const res = await axios.post(
        `${APP_URL}/schoolposition/delete/${school_has_position_id}`,
        {
          school_code: school_code,
        }
      );
      if (res) {
        this.setState({
          success: "Positions added successfully!",
          error: "",
        });
        this.props.removePositionsToSchool(school_has_position_id);
      }
    } catch (error: any) {
      console.log("error res: ", error.response?.data?.users);
      if (
        error.response?.data?.users !== undefined &&
        error.response.data.users.length > 0
      ) {
        const positionTemp = this.props.schoolPositions.find(
          (itm) => itm.school_has_position_id === school_has_position_id
        );
        const position = this.props.positions.find(
          (itm) => itm.position_code === positionTemp?.position_code
        );
        if (position !== undefined) {
          this.setState({
            selected_position_users_on_delete: {
              position: position,
              data: error.response?.data?.users as PositionStaffOnDelete[],
            },
          });
        } else {
          this.setState({
            error: "Invalid Position selected!",
          });
        }
      } else {
        this.setState({
          error: errorToText(error),
        });
      }
      this.setState({ loading: false });
      console.log("Err: ", { ...error });
    }
  };

  // Sort positions first by positions with available teacher and search
  PositionsSortByHavingTeacher = () => {
    let positionsTemp: SchoolPositionApiResponse[] = [];
    positionsTemp = this.props.positions.sort((a, b) => {
      const countOnA = this.props.schoolPositions.find(
        (itm) => itm.position_code === a.position_code
      );
      const countOnB = this.props.schoolPositions.find(
        (itm) => itm.position_code === b.position_code
      );
      if (countOnA !== undefined && countOnB !== undefined) {
        if (
          parseInt(countOnA.total_on_position) >
          parseInt(countOnB.total_on_position)
        ) {
          return -1;
        } else {
          return 1;
        }
      }
      return 0;
    });
    positionsTemp = positionsTemp.filter(
      (itm) =>
        this.props.schoolPositions.find(
          (test) => test.position_code === itm.position_code
        ) === undefined
    );
    return search(
      positionsTemp,
      this.state.searchPositionValue
    ) as SchoolPositionApiResponse[];
  };

  render() {
    return (
      <Fragment>
        <div>
          <div
            className="bg-white dark:bg-dark-800 p-3 rounded overflow-y-auto"
            style={
              this.state.selected_position_users_on_delete === null
                ? {}
                : { maxHeight: "80vh" }
            }
          >
            <div className="flex flex-row items-center justify-between">
              <div className="flex flex-row items-center gap-2 justify-between w-full">
                <span className="text-2xl font-bold text-primary-800">
                  {this.state.edit === true
                    ? "Assign positions to school"
                    : "School positions list"}
                </span>
                <div>
                  <Button
                    onClick={() => this.setState({ edit: !this.state.edit })}
                    theme={this.state.edit === true ? "default" : "accent"}
                    className={`flex flex-row items-center gap-3 animate__animated ${
                      this.state.edit === false
                        ? "animate__zoomIn"
                        : "animate__fadeInLeft"
                    }`}
                  >
                    <div>
                      {this.state.edit === true ? (
                        <MdArrowBack className="text-2xl" />
                      ) : (
                        <MdAdd className="text-2xl" />
                      )}
                    </div>
                    <span>
                      {this.state.edit === true
                        ? "Back to list"
                        : "Add new positions"}
                    </span>
                  </Button>
                </div>
              </div>
              {this.props.close !== undefined && (
                <div>
                  <div
                    onClick={() => this.props.close && this.props.close()}
                    className="bg-gray-100 hover:bg-yellow-100 flex items-center justify-center p-1 cursor-pointer rounded-full"
                  >
                    <MdClose className="text-3xl" />
                  </div>
                </div>
              )}
            </div>
            {this.state.success !== "" && (
              <div
                onClick={() => this.setState({ success: "" })}
                className="bg-green-600 text-white font-bold pl-4 pr-2 py-2 text-lg rounded-md my-2 fixed right-10 cursor-pointer shadow-xl animate__animated animate__bounceInRight"
              >
                <div className="flex flex-row justify-between items-center gap-8">
                  <div>Success: {this.state.success}</div>
                  <div>
                    <MdClose className="text-2xl text-red-600 bg-red-100 rounded-full" />
                  </div>
                </div>
              </div>
            )}
            {this.state.error !== "" && (
              <div
                onClick={() => this.setState({ error: "" })}
                className="bg-red-600 text-white font-bold pl-4 pr-2 py-2 text-lg rounded-md my-2 fixed right-10 cursor-pointer shadow-xl animate__animated animate__bounceInRight"
              >
                <div className="flex flex-row justify-between items-center gap-8">
                  <div>Error: {this.state.error}</div>
                  <div>
                    <MdClose className="text-2xl text-red-600 bg-red-100 rounded-full" />
                  </div>
                </div>
              </div>
            )}
            <div
              className={`grid md:grid-cols-${
                this.state.edit === true ? "2" : "1"
              }`}
            >
              <div
                className={`col-span-1 border-r pr-2 ${
                  this.state.edit === true ? "" : "hidden"
                }`}
              >
                {/* <div>
                <span className="text-sm">
                  Positions list, Select a position
                </span>
              </div> */}
                <div className="">
                  <TextInput
                    onChange={(e) =>
                      this.setState({ searchPositionValue: e.target.value })
                    }
                    error={""}
                    placeholder="Search position..."
                    value={this.state.searchPositionValue}
                    type="search"
                    name="post"
                    label="Search"
                  />
                </div>
                <div className="h-screen overflow-y-auto">
                  {this.PositionsSortByHavingTeacher().length === 0 ? (
                    <div>No positions found!</div>
                  ) : (
                    this.PositionsSortByHavingTeacher().map((item, i) => (
                      <div
                        key={i + 1}
                        className="border border-gray-300 px-3 py-2 flex flex- justify-between group bg-white hover:bg-blue-50 hover:border-blue-300 gap-3  items-center cursor-pointer my-1 rounded"
                        onClick={() => {
                          this.addPositionToSchool(item);
                        }}
                      >
                        <div className="flex flex-col">
                          <div className="flex flex-row items-center gap-1 text-base font-bold text-gray-700 group-hover:text-primary-800">
                            <div>
                              <div className="flex items-center justify-center font-normal rounded-full h-8 -ml-1 w-8 border border-gray-400 group-hover:border-primary-800 bg-gray-200 group-hover:bg-white group-hover:text-primary-800 text-sm">
                                {i + 1}
                              </div>
                            </div>
                            <span className="font-light">
                              {item.position_code}-
                            </span>
                            <span className="">
                              {item.position_name} ({item.qualification_name})
                            </span>
                          </div>
                          {/* <div className="text-sm text-gray-400">
                            School level:{" "}
                            <span className={`text-black`}>
                              {item.school_level_name}
                            </span>
                          </div> */}
                        </div>
                        <div className="flex flex-row items-center gap-2">
                          {this.props.schoolPositions.find(
                            (itm) => itm.position_code === item.position_code
                          ) !== undefined && (
                            <div className="bg-yellow-100 rounded-full text-sm font-bold px-2 border border-yellow-400 truncate">
                              Total:{" "}
                              {
                                this.props.schoolPositions.find(
                                  (itm) =>
                                    itm.position_code === item.position_code
                                )?.total_on_position
                              }
                            </div>
                          )}
                          <div className="bg-blue-100 text-primary px-3 py-1 rounded cursor-pointer text-base text-primary-800 group-hover:text-white group-hover:bg-primary-800">
                            Select
                          </div>
                        </div>
                      </div>
                    ))
                  )}
                </div>
              </div>
              {/* Right col */}
              <div className="col-span-1 px-3">
                {" "}
                {/* RiHome6Fill : School home icon later */}
                <div className="flex flex-col border-b pb-3 border-accent-900 mt-2">
                  {/* <span>School</span> */}
                  <div className="flex flex-row items-center justify-between">
                    <span className="text-accent-900 text-xl font-bold">
                      {this.props.school === undefined
                        ? this.props.auth.school?.school_name
                        : this.props.school.school_name}{" "}
                      (
                      {this.props.school === undefined
                        ? this.props.auth.school?.school_code
                        : this.props.school.school_code}
                      )
                    </span>
                    <div className="font-bold text-2xl">
                      {
                        [
                          ...this.state.schoolPositionsTemp,
                          ...this.props.schoolPositions,
                        ].length
                      }
                    </div>
                  </div>
                </div>
                {/* <div>
                <span className="text-primary-800 font-bold text-lg">
                  School positions
                </span>
              </div> */}
                {/* School positions */}
                <div className="mt-3">
                  {[
                    ...this.state.schoolPositionsTemp,
                    ...this.props.schoolPositions,
                  ].length === 0 ? (
                    <div className="flex flex-col bg-gray-100 w-full justify-center items-center rounded-md p-4">
                      <div className="text-7xl text-gray-400">
                        <FiSearch />
                      </div>
                      <span className="text-3xl text-black">
                        School positions not found
                      </span>
                      <span className="text-sm text-gray-600">
                        This school has no positions assigned to it. Please
                        select positions
                      </span>
                    </div>
                  ) : (
                    <div>
                      {this.state.schoolPositionsTemp.length !== 0 && (
                        <div className="text-xl text-primary-800 my-3 font-bold">
                          New pending
                        </div>
                      )}
                      {this.state.schoolPositionsTemp.map((item, i) => (
                        <div
                          key={i + 1}
                          className={`border rounded-md px-3 py-2 mb-2 group cursor-pointer ${
                            this.state.schoolPositionsTemp.find(
                              (itm) => itm.position_id === item.position_id
                            ) !== undefined
                              ? "bg-yellow-50 border-yellow-400"
                              : "bg-blue-50 border-blue-400 hover:bg-blue-100"
                          } `}
                        >
                          <div className="flex flex-row items-center justify-between">
                            <div className="flex flex-row items-center gap-1 text-base font-bold">
                              <div className="flex items-center justify-center font-normal rounded-full h-8 -ml-1 w-8 border border-gray-400 bg-gray-200 group-hover:bg-gray-500 group-hover:text-white text-sm">
                                {i + 1}
                              </div>
                              <span className="font-light">
                                {item.position_code}-
                              </span>
                              <span className="">
                                {item.position_name} ({item.qualification_name})
                              </span>
                            </div>

                            <div className="">
                              <div
                                onClick={() => {
                                  this.setState({
                                    schoolPositionsTemp:
                                      this.state.schoolPositionsTemp.filter(
                                        (itm) =>
                                          itm.position_id !== item.position_id
                                      ),
                                  });
                                }}
                                className="bg-white text-accent-900 px-3 py-1 rounded cursor-pointer text-base border border-yellow-400"
                              >
                                Remove
                              </div>
                            </div>
                          </div>

                          {/* <div className="text-sm">
                          School level:{" "}
                          <span
                            className={`font-bold ${
                              this.state.schoolPositionsTemp.find(
                                (itm) => itm.position_id === item.position_id
                              ) !== undefined
                                ? "text-accent-900"
                                : "text-primary-800"
                            }`}
                          >
                            {item.school_level_name}
                          </span>
                        </div> */}
                        </div>
                      ))}
                      {this.state.schoolPositionsTemp.length !== 0 && (
                        <div>
                          <Button
                            onClick={() => {
                              this.addSchoolPositions();
                            }}
                            theme="accent"
                          >
                            Submit positions
                          </Button>
                        </div>
                      )}
                      {this.props.schoolPositions.length !== 0 && (
                        <div className="font-bold text-lg text-gray-500 my-3">
                          List of school position
                        </div>
                      )}
                      {this.props.schoolPositions.map((item, i) => (
                        <div
                          key={i + 1}
                          className={`border rounded-md px-3 py-2 mb-2 group cursor-pointer ${
                            this.state.schoolPositionsTemp.find(
                              (itm) => itm.position_code === item.position_code
                            ) !== undefined
                              ? "bg-yellow-50 border-yellow-400"
                              : "bg-gray-100 hover:bg-blue-100"
                          } flex flex-row items-center justify-between gap-4`}
                        >
                          <div className="">
                            <div className="flex flex-row items-center justify-between">
                              <div className="flex flex-row gap-1 items-center text-base font-bold">
                                <div>
                                  <div className="flex items-center justify-center font-normal rounded-full h-8 -ml-1 w-8 border border-blue-400 bg-blue-100 text-sm">
                                    {i + 1}
                                  </div>
                                </div>
                                <span className="font-light">
                                  {item.position_code}-
                                </span>
                                <span className="">
                                  {item.position_name} (
                                  {item.qualification_name})
                                </span>
                              </div>
                            </div>
                          </div>
                          <div className="flex flex-row items-center gap-2">
                            <div className="bg-yellow-100 rounded-full text-sm font-bold px-2 border border-yellow-400 truncate">
                              Total:{" "}
                              {
                                this.props.schoolPositions.find(
                                  (itm) =>
                                    itm.position_code === item.position_code
                                )?.total_on_position
                              }
                            </div>
                            {this.props.schoolPositions.find(
                              (itm) => itm.position_code === item.position_code
                            ) !== undefined &&
                              parseInt(
                                this.props.schoolPositions.find(
                                  (itm) =>
                                    itm.position_code === item.position_code
                                )!.total_on_position
                              ) <= 0 && (
                                <div
                                  onClick={() => {
                                    if (
                                      item.school_code !== "" &&
                                      window.confirm(
                                        "Are you sure do you want to remove this position"
                                      ) === true
                                    ) {
                                      this.RemoveSchoolPosition(
                                        item.school_has_position_id,
                                        item.school_code
                                      );
                                    }
                                  }}
                                  className="bg-white text-red-800 group-hover:text-white group-hover:bg-red-600 px-3 py-1 font-bold text-center rounded -mr-1"
                                >
                                  Delete
                                </div>
                              )}
                            {this.props.schoolPositions.find(
                              (itm) => itm.position_code === item.position_code
                            ) !== undefined &&
                              parseInt(
                                this.props.schoolPositions.find(
                                  (itm) =>
                                    itm.position_code === item.position_code
                                )!.total_on_position
                              ) > 0 && (
                                <div
                                  onClick={() =>
                                    this.RemoveSchoolPosition(
                                      item.school_has_position_id,
                                      item.school_code
                                    )
                                  }
                                  className="bg-blue-100 border border-white text-primary-800 group-hover:border-primary-800 hover:border-primary-800 group-hover:bg-primary-800 group-hover:text-white px-3 py-1 font-bold text-center rounded -mr-1"
                                >
                                  Staffs
                                </div>
                              )}
                          </div>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
        {this.state.selected_position_users_on_delete !== null && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={() =>
              this.setState({ selected_position_users_on_delete: null })
            }
            backDropClose={false}
            widthSizeClass={ModalSize.extraLarge}
            displayClose={false}
            padding={{
              body: true,
            }}
          >
            {this.state.loading === true ? (
              <div className="bg-white rounded p-4">
                <Loading />
                <div className="text-xl font-light ml-4 -mt-10">
                  Loading, please wait...
                </div>
              </div>
            ) : (
              <div className="bg-white">
                <div className="flex flex-row gap-3 justify-between">
                  <div>
                    <div className="font-extrabold text-2xl">
                      {
                        this.state.selected_position_users_on_delete.position
                          .position_name
                      }
                    </div>
                    <div>List of staffs for the selected position</div>
                  </div>
                  <div
                    onClick={() =>
                      this.setState({ selected_position_users_on_delete: null })
                    }
                    className="bg-accent-800 font-bold cursor-pointer px-3 py-2 rounded truncate w-max h-10 flex items-center justify-center"
                  >
                    Close window
                  </div>
                </div>
                <div className="mt-3">
                  <table className="w-full text-left text-base font-light">
                    <thead className="bg-primary-100">
                      <tr>
                        <th className="px-3 py-1">#</th>
                        <th className="px-3 py-1">Staff code</th>
                        <th className="px-3 py-1">Full name</th>
                        <th className="px-3 py-1">NID</th>
                        <th className="px-3 py-1">Phone number</th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.selected_position_users_on_delete.data.map(
                        (item, i) => (
                          <tr
                            key={i + 1}
                            className={`${i % 2 !== 0 ? "bg-gray-100" : ""}`}
                          >
                            <td className="px-3 py-1">{i + 1}</td>
                            <td className="px-3 py-1">{item.staff_code}</td>
                            <td className="px-3 py-1">{item.full_name}</td>
                            <td className="px-3 py-1">{item.nid}</td>
                            <td className="px-3 py-1">{item.phone_numbers}</td>
                          </tr>
                        )
                      )}
                    </tbody>
                  </table>
                </div>
                <div className="mt-3 w-full">
                  <div className="flex flex-row gap-2 w-full bg-accent-400 rounded-md p-4 border border-yellow-400">
                    <div>
                      <MdInfoOutline className="text-5xl" />
                    </div>
                    <div className="text-xl font-light text-black flex flex-col">
                      <span className="font-extrabold">
                        You can not remove this position!
                      </span>
                      <span className="text-base">
                        If you want to remove this position, you need to check
                        these staffs before. Thus, if you decided to remove it,
                        please remove these staffs before removing this
                        position!
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </Modal>
        )}
      </Fragment>
    );
  }
}

export default AddSchoolPositions;
