import React, { FC, ReactElement, useState } from "react";
// import axios from "axios";
// import TextInput from "../../../components/Inputs/TextInput";
// import { FaAngleDown } from "react-icons/fa";
import { PostRequestReasons } from "../../../types/HeadTeacherRequesTypes";
// import { setAxiosToken } from "../../../utils/AxiosToken";
// import { APP_URL } from "../../../config/app.config";
// import { InputSizeType } from "../../../components/Inputs/InputTypes";
import { PositionsOnSchool, PostRequestItem } from "./HeadTeacherRequestToDee";

type AddActionInputProps = {
  postRequestReasons: PostRequestReasons[];
  getPostRequestItem: (data: {
    position: PositionsOnSchool;
    reason: string;
    postsNumber: number;
  }) => PostRequestItem[];
  close: () => void;
  currentRequests: number;
  submitRequest: (addedRequests: PostRequestItem[]) => void;
  position: PositionsOnSchool;
};

const AddActionInput: FC<AddActionInputProps> = (
  props: AddActionInputProps
): ReactElement => {
  const [reason, setReason] = useState<string>("");
  const [inputValue, setInputValue] = useState<string>(
    props.currentRequests.toString()
  );
  const [error, setError] = useState<string | null>(null);
  return (
    <>
      <input
        type="number"
        className="w-20 p-1 border-4 rounded-t border-primary-800 font-bold px-3 text-center text-black"
        value={inputValue}
        name="POSTSS"
        onChange={(e) => {
          setError(null);
          setInputValue(e.target.value);
        }}
      />
      <div className="animate__animated animate__zoomIn animate__faster absolute p-1 bg-primary-800 rounded-b -mt-0.5 rounded-tl  z-40 w-80 right-0 ">
        <label
          className="px-2 text-sm text-gray-300"
          htmlFor="selected_comment"
        >
          Request reason
        </label>
        <div className="px-2">
          <select
            value={reason}
            placeholder="Select reason"
            className="block appearance-none w-full bg-white border-2 border-primary-800 hover:border-gray-500 px-4 py-2 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline md:w-full text-black"
            onChange={(e) => {
              setError(null);
              setReason(e.target.value);
            }}
          >
            <option value={"Select reason"}>Select reason</option>
            {props.postRequestReasons.map((element) => {
              return (
                <option key={element.reason_id} value={element.reason_id}>
                  {element.reason_name}
                </option>
              );
            })}
          </select>
        </div>
        {error !== null && (
          <div
            onClick={() => setError(null)}
            className="bg-red-100 rounded-md text-red-600 px-2 py-2 mx-3 my-2 animate__animated animate__animated animate__bounceIn"
          >
            {error}
          </div>
        )}
        <div className="flex">
          <button
            onClick={() => {
              if (inputValue.length < 0) {
                setError("Please number of requests should be greater than 0");
              } else if (reason.length < 0) {
                setError("Please select reason for number of requested posts");
              } else {
                setError(null);
                if (reason === "") {
                  setReason(props.postRequestReasons[0].reason_id);
                }
                const res = props.getPostRequestItem({
                  position: props.position,
                  reason: reason,
                  postsNumber: parseInt(inputValue),
                });
                console.log("Send: ", res);
                props.submitRequest(res);
                props.close();
              }
            }}
            className="p-1 px-4 rounded text-white bg-green-400 hover:bg-green-500 mx-2 my-1"
          >
            Send request
          </button>
          <button
            onClick={() => props.close()}
            className="p-1 px-2 rounded text-white bg-red-600 hover:bg-red-600  my-1"
          >
            Cancel
          </button>
        </div>
      </div>
    </>
  );
};

export default AddActionInput;
