export const totalItemPosts = (
  schoolPositions: PositionsOnSchool[],
  element: PositionsOnSchool
): number => {
  let totalExistingPostsStatic: number = 0;
  let testItem = schoolPositions.find(
    (test) => test.position_code === element.position_code
  );
  if (testItem !== undefined) {
    totalExistingPostsStatic =
      testItem.existing_post === undefined
        ? 0
        : parseInt(testItem.existing_post);
  }

  const totalDistributed: number =
    element.dde_post_distribution === null ||
    element.dde_post_distribution === undefined
      ? 0
      : parseInt(element.dde_post_distribution);

  return totalExistingPostsStatic + totalDistributed;
};

export const vacantPosts = (
  normalSchoolPositions: PositionsOnSchool[],
  schoolPositions: PositionsOnSchool[],
  element: PositionsOnSchool
): number => {
  let total = 0;

  let testPosition = normalSchoolPositions.find(
    (test) => test.position_code === element.position_code
  );
  if (testPosition !== undefined) {
    total =
      totalItemPosts(schoolPositions, element) -
      parseInt(testPosition.total_on_position);
  }
  return total;
};

